import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';

import { NewCategoryService } from '../../angular-services/new-category.service';
import { NewCategory } from '../../classes/new-category';

@Component({
  selector: 'app-edit-news-category-form-admin',
  templateUrl: './edit-news-category-form.component.html',
  styleUrls: ['./edit-news-category-form.component.css']
})
export class EditNewsCategoryFormComponent implements OnInit {

  @Output() categoryTitle = new EventEmitter<string>();
  formEditCategory: FormGroup;
  category: NewCategory = new NewCategory();
  alert: any = { type: '', message: '', show: false };

  constructor(private newCategoryService: NewCategoryService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.initForm();
    this.getCategory();
  }

  initForm() {
    this.formEditCategory = new FormGroup({
      title: new FormControl('', Validators.required),
      status: new FormControl('', Validators.required),
    });
  }

  getCategory() {
    let key = this.route.snapshot.paramMap.get('id');
    this.newCategoryService.getCategory(key).subscribe(
      response => {
        if (response !== undefined) {
          this.category = response;
          this.categoryTitle.emit(this.category.title);
          this.loadForm();
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao salvar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  loadForm() {
    this.formEditCategory.patchValue({
      title: this.category.title,
      status: this.category.status+''
    });
  }

  save() {
    if (this.formEditCategory.valid) {
      let key = this.route.snapshot.paramMap.get('id');
      let data = this.formEditCategory.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.newCategoryService.updateCategory(key, data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }

}
